/* SCSS Document */

@charset "utf-8";

@import "../../object/component/_color";
@import "../../object/component/_font";
@import "../../object/component/_grid";
.o-card{
    max-width: 565px;
    min-height: 565px;
        &--wide{
            max-width: 760px;
            min-height: 565px;
        }
}
.contents--center__main {
    & .o-card{
        max-width: 565px;
        min-height: 565px;
        &--wide{
            max-width: 760px;
            min-height: 565px;
        }
    }
    & .o-card__serviceimgtitle {
        color: $lightgrey--light;
    }
    & .o-card__serviceimg {
        width: 400px;
        max-width: 100%;
    }
    & .notice {
        max-width: 600px;
        background-color: $puregrey;
        padding: 20px;
        align-items: stretch;
    }
    & .notice__row {
        display: flex;
        flex-direction: row;
        align-items: flex-start;
        & > i {
            color: $orange;
        }
        & > div,
        & > a {
            line-height: 24px;
        }
    }
}

////////// Desktop //////////
@media (min-width: $grid-desktop-breakpoint) and (max-width: $grid-menudesktop-breakpoint - 1) {
    #js-headermenu {
        display: none;
    }
}

////////// MenuDesktop //////////
@media (min-width: $grid-menudesktop-breakpoint) and (max-width: $grid-bigdesktop-breakpoint - 1) {
    #js-headermenu {
        display: none;
    }
}

////////// BigDesktop //////////
@media (min-width: $grid-bigdesktop-breakpoint) {
    #js-headermenu {
        display: none;
    }
}
